import * as React from 'react'
import { Link } from 'gatsby'
import {
    navLinks,
    navLinkItem,
    navLinkText,
} from '../components/layout.module.css'
import Layout from '../components/layout'

// Step 2: Define your component
const AboutPage = () => {
    return (
        <Layout pageTitle="About">
            <p>I am a huge collection of atoms called Karthikeshwar.</p>
            <br />
            <nav>
                <ul className={navLinks}>
                    <li className={navLinkItem}><Link to="https://github.com/Karthikeshwar1" className={navLinkText}>GitHub</Link></li>
                    <li className={navLinkItem}><Link to="https://linkedin.com/in/karthikeshwar/" className={navLinkText}>LinkedIn</Link></li>
                    {/* <li className={navLinkItem}><Link to="/blog" className={navLinkText}>Blog</Link></li> */}
                    <li className={navLinkItem}><Link to="https://spotify.com" className={navLinkText}>Spotify</Link></li>
                </ul>
            </nav>
        </Layout>
    )
}

// Step 3: Export your component
export default AboutPage